<template>
    <div class="checkDetail" >
        <navigation-bar :pageName="title" @onLeftClick="goBack">
        </navigation-bar>
        <div class="box">
           <div class="work-content">
                <div class="title" v-show="labelList && labelList.length > 0">选择工单标签</div>
                <div class="type">
                    <div @click="selectWorkPlanLabel(index)" v-for="(item,index) in labelList" :key="index" v-bind:class="{active: index == labelIndex}">
                      {{item.labelName}}
                    </div>
                </div>
                <div class="person-info">
                    <van-field
                           clickable
                           label="报修人"
                           v-model="repairPersonInfo.repairName"
                           input-align="right"
                           placeholder="请选择报修人姓名"
                           :right-icon= "queryIcon"
                           @click-right-icon="showRepairPersonPopup"
                           :readonly="false"/>
                    <van-field
                           clickable
                           label="报修人电话"
                           v-model="repairPersonInfo.repairTel"
                           input-align="right"
                           placeholder="请选择报修人电话"
                           :right-icon= "queryIcon"
                           @click-right-icon="showRepairPersonPopup"
                           :readonly="false"/>
                </div>
                <div class="area-box">
                    <textarea name="" id="" placeholder="请输入报修内容..." v-model="workContent" maxlength="100" @input="filterContent" class="content-textarea"></textarea>
                    <div class="voice-handle" v-if="voiceInputEnabled">
                        <van-row span="8" type="flex" justify="space-between">
                            <van-col span="8" class="desc" id="voice">
                              <span class="cls-voice">
                              <img class="cls-icon" src="../../assets/imgs/find/ic_yuyin.png" alt="">
                                语音录入
                              <img class="voice-input-hint" src="@/assets/imgs/find/search_voice.gif" alt="" v-show="showTips">
                              </span>
                            </van-col>
                            <van-col >
                              <p class="input-length">{{workContent.length}}/100</p>
                            </van-col>
                        </van-row>
                    </div>
                    <div class="num" v-else>{{workContent.length}}/100</div>
                </div>
            </div>
            <div class="photo-box">
                <div class="title">
                    <div>上传图片</div>
                    <div class="limit">(最多4张)</div>
                </div>
                <div class="photo">
                    <add-image v-on:imageAddAction="showAddSheetControll" v-on:imageMapChange="upDateImageData" ref="addImageComponent"></add-image>
                </div>
            </div>
            <div class="submit" @click="newRepair">提交</div>
        </div>

        <van-popup v-model="showRepairPersonInfo" position="bottom" >
            <div class="repair-box">
                <div class="title">
                    <div class="left">取消</div>
                    <div class="center">选择业主</div>
                    <div class="right">确定</div>
                </div>
                <div class="content">
                    <div class="item" v-for="(item, index) in ownerList" :key="index" @click="selectOwner(item)">
                        <div>{{item.ownerName}}</div>
                        <div>{{item.phone}}</div>
                    </div>
                </div>
            </div>
        </van-popup>
        <van-action-sheet
          v-model="showAddSheet"
          :actions="actions"
          cancel-text="取消"
          round
          close-on-click-action
          @select="addSelect"/>
    </div>
</template>

<script>
import {
  getWorkPlanLabelList,
  saveWorkPlan
} from "../../services/workPlanService";
import {
  getRoomOwnerList,
} from "../../services/homePageService";
import { store } from "../../utils/store";
import log from "../../utils/log";
import AddImageVue from "../addImage/AddImage.vue";
import queryIcon from '@/assets/imgs/workPlan/query.png';
import NavigationBar from "../title/NavigationBar";
import { Toast, Dialog } from "vant";
export default {
  name: "newRepair",
  data() {
    return {
      // 传递的参数
      roomDetail: null,
      userInfo: null,
      actions: [{ name: "拍照" }, { name: "从手机相册选择" }],
      imgList: [],
      showAddSheet: false,
      title: "",
      labelList: [],
      labelIndex: -1,
      workContent: "",

      repairType: {"wayName":"业主电话报修","wayCode":0},
      queryIcon: queryIcon,
      showRepairPersonInfo: false,

      // 报修人信息
      repairPersonInfo: {
          repairCode: "",
          repairName: "",
          repairTel: ""
      },
      // 业主列表
      ownerList: [],
      sendVal:false,
      speechStopped: true,
      posX: 0,
      posY: 0,
      showTips: false,
      waitStop: false,
      voiceInputEnabled: false,
    };
  },
  components: {
    "add-image": AddImageVue,
    NavigationBar
  },
  created: function() {
    this.userInfo = store.get("userInfo");
    this.roomDetail = this.$route.params;
    this.title =
      this.roomDetail.BUILD_NAME +
      " " +
      this.roomDetail.UNIT_NO +
      " " +
      this.roomDetail.ROOM_NAME +
      "室房间报修";
    this.getWorkPlanLabelList();
    this.getRoomOwnerList();
  },
  mounted() {
    if (this.voiceInputEnabled) {
      let oDiv = document.getElementById('voice');
      oDiv.addEventListener("touchstart", e => {
          this.speechStopped = false;
          this.goToSpeech(e);
          this.posX = e.touches[0].clientX;
          this.posY = e.touches[0].clientY;
      }, false);
      oDiv.addEventListener('touchmove', e => {
          if (!this.speechStopped) {
            if (Math.abs(this.posX - e.touches[0].clientX) > 30 || Math.abs(this.posY - e.touches[0].clientY) > 30) {
              this.speechStopped = true;
              this.stopSpeech("滑动操作");
            }
          }
      }, false);
      oDiv.addEventListener("touchend", e => {
          if (!this.speechStopped) {
            this.stopSpeech();
            this.posX = 0;
            this.posY = 0;
          }
      }, false);
      let audioPermitted = !store.get("audioPermitted") ? false : store.get("audioPermitted");
      if (!audioPermitted) {
          this.requestAudioPermission();
      }
    }
  },
  beforeRouteLeave(to, from, next) {
    next();
  },
  activated() {},
  methods: {
    /**
     * 页面返回事件
     */
    goBack: function() {
      if (store.get("reloadCheckHouseStatus") != 2) {
        // 没点提交
        store.set("reloadCheckHouseStatus", 0);
      }
      if (this.roomDetail.flag == 1) {
        // 判断是不是从验房详情页面进入的
        this.$router.replace({
          name: "handCheck",
          params: this.roomDetail
        });
      } else {
        this.$router.back(-1);
      }
    },

    /**
     * 获取工单标签
     */
    getWorkPlanLabelList: function() {
      getWorkPlanLabelList({
        type: 2,
        deptCode: this.userInfo.deptCode,
      }).then(
        res => {
          if(res.resultCode == 0) {
            this.labelList = res.data;
          } else {
            this.$toast(res.msg)
          }
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 选择标签事件
     */
    selectWorkPlanLabel: function(index) {
      this.labelIndex = index;
    },

    /**
     * 子组件更新已选图片集合监听函数
     **/
    upDateImageData(newImageData) {
      this.imgList = newImageData;
    },

    /**
     * addImage组件添加照片时的监听函数父组件用来选择哪种图片获取方式(拍照或相册上传)
     **/
    showAddSheetControll() {
      this.showAddSheet = !this.showAddSheet;
    },

    /**
     * 选择当前图片来源方式(拍照或本地上传)
     * @param
     * item      Object   当前选择的方式对象
     * index     Number   当前选择的索引
     **/
    addSelect(item, index) {
      this.getPictureData(index);
    },

    /**
     * 调用摄像头或者本地相册进行图片上传
     * @param
     **/
    getPictureData(selectIndex) {
      selectIndex
        ? this.$refs.addImageComponent.getPicture()
        : this.$refs.addImageComponent.useCamera();
    },

    /**
     * 禁止输入表情
     */
    filterContent: function() {
      this.workContent = this.workContent.replace(
        /[\uD83C|\uD83D|\uD83E][\uDC00-\uDFFF][\u200D|\uFE0F]|[\uD83C|\uD83D|\uD83E][\uDC00-\uDFFF]|[0-9|*|#]\uFE0F\u20E3|[0-9|#]\u20E3|[\u203C-\u3299]\uFE0F\u200D|[\u203C-\u3299]\uFE0F|[\u2122-\u2B55]|\u303D|[\A9|\AE]\u3030|\uA9|\uAE|\u3030/gi,
        ""
      );
    },

    /**
     * 创建新的报修
     */
    newRepair: function() {
      if (!this.hasCompleteParam()) {
        return;
      }
      var repairData = {
        terminalType: 1, //终端类型 1=物业端
        areaName: this.userInfo.AREA_NAME,
        areaCode: this.userInfo.AREA_CODE,
        buildCode: this.roomDetail.BUILD_CODE,
        buildName: this.roomDetail.BUILD_NAME,
        roomCode: this.roomDetail.ROOM_CODE,
        roomName: this.roomDetail.ROOM_NAME,
        unitNo: this.roomDetail.UNIT_NO,
        // ownerName: this.repairPersonInfo.repairName,//报修人
        // ownerPhone: this.repairPersonInfo.repairTel,//报修人
        userCode: this.userInfo.USER_CODE,
        content: this.workContent,
        reportType: 0,//0业主提报 1物业提报
        typeFlag: 2, //0报修 1投诉 2验房
        picUrl: this.imgList,
        createdBy: this.userInfo.USER_CODE,
        postCode: this.userInfo.POST_CODE,
        labelCode:
          this.labelIndex > -1 ? this.labelList[this.labelIndex].labelCode : "", //默认验房label
        WJDM: ""
      };
      if (!this.repairPersonInfo.repairName) {
          // 没有选择报修人，使用提报人信息，物业提报
          repairData.reportType = 1;
          repairData.ownerName = this.userInfo.USER_NAME;
          repairData.ownerPhone = this.userInfo.PHONE;
      } else {
          // 选择了报修人，使用报修人信息，业主提报
          repairData.reportType = 0;
          repairData.ownerName = this.repairPersonInfo.repairName;
          repairData.ownerPhone = this.repairPersonInfo.repairTel;
      }
      saveWorkPlan(repairData).then(
        res => {
          if (res.resultCode == "0") {
            this.$toast("提交成功");
            store.set("reloadCheckHouseStatus", 2);
            store.set("checkListSingleData", {
              ROOM_CODE: this.roomDetail.ROOM_CODE,
              DELIVE_FLAG: "0"
            });
            this.goBack();
          } else {
            this.$toast(res.msg);
          }
        },
        error => {
          this.$toast(error);
        }
      );
    },

      /**
       * 获取房间管理者列表
       */
      getRoomOwnerList: function() {
        getRoomOwnerList({
            roomCode: this.roomDetail.ROOM_CODE
        }).then(
            res => {
                if(res.resultCode == 0) {
                    this.ownerList = res.data.ownerList;
                }
            },
            error => {
                this.$toast(error);
            }
        );
    },

      /**
       * 选中报修人信息
       */
      selectOwner: function(item) {
          this.repairPersonInfo.repairCode = item.ownerCode;
          this.repairPersonInfo.repairName = item.ownerName;
          this.repairPersonInfo.repairTel = item.phone;
          this.showRepairPersonInfo = false;
      },

      /**
       * 展示报修人信息弹窗
       */
      showRepairPersonPopup: function() {
          this.showRepairPersonInfo = true;
      },

    /**
     * 判断是否必选的参数填写完
     */
    hasCompleteParam: function() {
      if (this.labelIndex < 0) {
        this.$toast("请选择工单标签！");
        return false;
      }
      /*if (!this.repairPersonInfo.repairName) {
        this.$toast("请选择报修人！");
        return false;
      }*/
      if (!this.workContent) {
        this.$toast("请填写工单内容！");
        return false;
      }
      return true;
    },
    stopSpeech(msg){
        var _this = this;
        var ifly = cordova.plugins.IflyForXwjPlugin;
        if(_this.sendVal){
            _this.waitStop = true;
            if (msg) {
              _this.$toast(msg + ", 结束收音，正在识别。");
            } else {
              _this.$toast("长按停止，收音结束，正在识别。");
            }
            setTimeout (() => {
                if (_this.waitStop) {
                  _this.sendVal = false;
                  ifly.stopListen(null,null,null);
                  console.log("停止收音" + new Date().toString());
                }
            }, 3000);
        } else {
            Toast('说话时间太短，请长按说话');
            _this.sendVal = false;
        }
        //console.log("停止录音 "+ this.pressTimer);
        _this.showTips = false;
        clearTimeout(this.pressTimer);
        this.pressTimer = null;
    },
    //语音搜索
    goToSpeech(e) {
        var _this = this;
        this.pressTimer = null;
        var ifly = cordova.plugins.IflyForXwjPlugin;
        this.$toast("正在聆听");
        this.showTips = true;
        const platform = navigator.userAgent;
        const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
        if(e.type === 'click' && e.button !==0){
            return;
        }
        if(this.pressTimer == null) {
          this.startSpeech(isiOS, ifly, false);
        }
    },
    startSpeech(isiOS, ifly, cycle) {
        var _this = this;
        this.pressTimer = setTimeout(()=>{
            if (!cycle) {
              console.log("start speech from touch action, set send val true.");
              _this.sendVal=true;
              if (_this.waitStop) {
                  // if wait stop now, should stop and reset wait stop state first.
                  _this.waitStop = false;
                  ifly.stopListen(null,null,null);
              }
            }
            if (isiOS) {
                ifly.startListen(function (result) {
                    if (result){
                        if (_this.sendVal){
                            var resultcatch = result.replace(/[\ |\~|\`|\!|\@|\#|\$|\%|\^|\&|\*|\(|\)|\-|\_|\+|\=|\||\\|\[|\]|\{|\}|\;|\:|\"|\'|\,|\<|\。|\>|\/|\？]/g,"");
                            let index = document.querySelector('.content-textarea').selectionStart;
                            let shouldScrollToEnd = index == _this.workContent.length;
                            let frontStr = _this.workContent.substring(0, index);
                            let backStr = _this.workContent.substring(index);
                            let newIndex = index + resultcatch.length;
                            let len = frontStr.length + resultcatch.length + backStr.length;
                            if (len <= 100) {
                              _this.workContent = frontStr + resultcatch + backStr;
                            } else {
                              let res = frontStr + resultcatch + backStr;
                              _this.workContent = res.substring(0, 100);
                            }
                            if (shouldScrollToEnd) {
                              document.querySelector('.content-textarea').scrollTop = 5000;
                            }
                            setTimeout(()=> {
                                document.querySelector('.content-textarea').selectionStart = newIndex;
                            }, 500);
                        }
                    }else {
                        console.log('没有值');
                    }
                    if (_this.sendVal) {
                      // restart voice catch before touch end
                      this.startSpeech(isiOS, ifly, true);
                    }
                },
                function (message) {
                    console.log("err" + message);
                    if (_this.sendVal) {
                      // restart voice catch before touch end
                      this.startSpeech(isiOS, ifly, true);
                    }
                }, false, true);
            } else {
                let audioPermitted = !store.get("audioPermitted") ? false : store.get("audioPermitted");
                if (!audioPermitted) {
                  this.$dialog.alert({
                      title: "授权语音权限",
                      message: "使用语音输入功能需要您先授权语音权限。",
                      showCancelButton: true
                  })
                  .then(() => {
                     // 请求语音权限
                     _this.requestAudioPermission();
                  })
                  .catch(() => { //点击取消按钮后的调用
                     console.log("点击了取消按钮");
                  })
                  // show dialog to request permission
                  return;
                }
                var list = [
                    'android.permission.RECORD_AUDIO'
                ];
                var permissions = cordova.plugins.permissions;
                permissions.requestPermissions(list,function (status) {
                    if(status.hasPermission){
                        ifly.startListen(function (result) {
                            if (result){
                                if (_this.sendVal){
                                    console.log(result + new Date().toString());
                                    var resultcatch = result.replace(/[\ |\~|\`|\!|\@|\#|\$|\%|\^|\&|\*|\(|\)|\-|\_|\+|\=|\||\\|\[|\]|\{|\}|\;|\:|\"|\'|\,|\<|\。|\>|\/|\？]/g,"");
                                    let index = document.querySelector('.content-textarea').selectionStart;
                                    let shouldScrollToEnd = index == _this.workContent.length;
                                    let frontStr = _this.workContent.substring(0, index);
                                    let backStr = _this.workContent.substring(index);
                                    let newIndex = index + resultcatch.length;
                                    let len = frontStr.length + resultcatch.length + backStr.length;
                                    if (len <= 100) {
                                      _this.workContent = frontStr + resultcatch + backStr;
                                    } else {
                                      let res = frontStr + resultcatch + backStr;
                                      _this.workContent = res.substring(0, 100);
                                    }
                                    if (shouldScrollToEnd) {
                                      document.querySelector('.content-textarea').scrollTop = 5000;
                                    }
                                    setTimeout(()=> {
                                        document.querySelector('.content-textarea').selectionStart = newIndex;
                                    }, 500);
                                }
                            }else {
                                console.log('没有值');
                            }
                            if (_this.sendVal) {
                              // restart voice catch before touch end
                              _this.startSpeech(isiOS, ifly, true);
                            }
                        },
                        function (message) {
                            console.log("err" + message);
                            if (_this.sendVal) {
                              // restart voice catch before touch end
                              _this.startSpeech(isiOS, ifly, true);
                            }
                        }, false, true);
                    }
                },function (status) {
                    alert("请打开麦克风权限");
                });
            }
        },300);
    },
    requestAudioPermission() {
      const platform = navigator.userAgent;
      const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
      if (!isiOS) {
        var list = [
            'android.permission.RECORD_AUDIO'
        ];
        var permissions = cordova.plugins.permissions;
        permissions.requestPermissions(list,function (status) {
            if (status.hasPermission) {
                store.set('audioPermitted', true);
            } else {
                store.set('audioPermitted', false);
            }
        },function (status) {
            store.set('audioPermitted', false);
            this.$toast.alert("请打开麦克风权限");
        });
      }
    }
  },
  computed: {}
};
</script>
